#cover {
    min-height: 100vh;
    display: flex;
    column-gap: 50px;
    align-items: center;
    padding-top: 80px;
}

.cover__title {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cover__title h1 {
    font-size: 44px;
}

.cover__title p {
    font-size: 24px;
}

.cover__form {
    display: flex;
    justify-content: center;
}

@media (max-width: 526px) {
    .cover__title h1 {
        font-size: 22px;
    }
}