#feedback__form {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    padding: 40px 20px;
    width: 350px;

    background-color: #fff;
    border-radius: 5px;

    -webkit-box-shadow: 0 5px 10px 2px rgba(255, 255, 255, 0.2);
    -moz-box-shadow: 0 5px 10px 2px rgba(255, 255, 255, 0.2);
    box-shadow: 0 5px 10px 2px rgba(255, 255, 255, 0.2);
}

#feedback__form h3 {
    text-align: center;
    text-transform: uppercase;

    font-family: 'Lato', sans-serif;
    font-weight: 400;
    font-size: 22px;
    color: #000;

}

.feedback__form-box {
    display: flex;
    flex-direction: column;
}

#feedback__form input, #feedback__form textarea {
    outline: none;
    border: 1px solid;
    border-radius: 5px;

    font-size: 16px;
    padding: 12px 10px;

    transition: .3s;

    -webkit-box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
}

#feedback__form input::-webkit-outer-spin-button,
#feedback__form input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

#feedback__form input::placeholder, #feedback__form textarea::placeholder {
    color: #737272;
}

#feedback__form input:focus,#feedback__form input:hover,
#feedback__form textarea:focus,#feedback__form textarea:hover {
    border: 1px solid #465bc9;
}

#feedback__form textarea {
    resize: none;
}

#feedback__form button {
    padding: 12px;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;

    background-color: #fff;
    outline: none;
    border-radius: 5px;
    border: 1px solid #000;
    transition: .3s;
}

#feedback__form label {
    color: red;

}

#feedback__form button:hover, .btn-true {
    background-color: rgba(0, 40, 255, 0.5)!important;
}

.btn-true {
    cursor: no-drop;
}