#header {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 10;
    background-color: #3a3a3a;
    padding: 10px 0;
}

#header .row {
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo__box {
    text-transform: uppercase;
    cursor: pointer;
}

.header__nav ul {
    display: flex;
    column-gap: 30px;
}

.header__nav ul li{
    cursor: pointer;
    opacity: 0.4;
    transition: all 0.3s;

    position: relative;
}
.header__nav ul li:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 0;
    background-color: #ffffff;

    transition: all 0.6s;
}

.header__nav ul li:hover:after {
    width: 100%;
}

.header__nav ul li:hover {
    opacity: 1;
}
.contacts {
    position: relative;
    display: flex;
    justify-content: center;
}

.contacts p {
    cursor: pointer;
}

.contacts span {
    position: absolute;
    top: -20px;

    padding: 2px 20px;
    background-color: #fff;
    border-radius: 40px;
    color: #000;
    font-size: 14px;
    font-weight: 600;
    display: none;
}

.contacts:hover > span {
    display: block;
}